section.exl-scrollable {
  position: relative;
  height: 100%;
}

section.exl-scrollable > div.exl-scrollable-container {
  width: 100%;
  height: 100%;
  overflow-x: visible;
  overflow-y: auto;

  box-sizing: border-box;
  position: relative;

  -ms-overflow-style: none;
}

section.exl-scrollable > div.exl-scrollable-container::-webkit-scrollbar {
  display: none;
}

/* section.exl-scrollable > div.exl-scrollable-container > div.exl-scrollable-content {
  내부 요소의 margin에 의한 레이아웃 오프셋이 스크롤을 망치지 않으려면 이부분이 overflow: auto; 여야한다.
  overflow: auto;
} */

section.exl-scrollable > div.exl-scrollable-scrollbar-wrapper {
  position: absolute;
  top: 2px;
  right: 0;
  padding: 0 1px;
  box-sizing: border-box;
  bottom: 2px;
  width: 10px;
  opacity: 0;
  overflow: visible;
  z-index: 10000;
  background-color: rgba(240, 240, 240, 0.3);
  transition: opacity 0.2s ease;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

section.exl-scrollable > div.exl-scrollable-scrollbar-wrapper.disappear {
  opacity: 0;
}

section.exl-scrollable > div.exl-scrollable-scrollbar-wrapper.appear {
  opacity: 1;
}

section.exl-scrollable > div.exl-scrollable-scrollbar-wrapper:hover {
  opacity: 1;
}

section.exl-scrollable > div.exl-scrollable-scrollbar-wrapper:active {
  opacity: 1;
}

section.exl-scrollable > div.exl-scrollable-scrollbar-wrapper > div.exl-scrollable-scrollbar-thumb {
  position: absolute;
  right: 2px;
  width: 6px;
  height: 6px;
  background-color: rgba(102, 102, 102, 0.8);
  transition: height 0.4s ease-in-out;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

section.exl-scrollable > div.exl-scrollable-scrollbar-wrapper > div.exl-scrollable-scrollbar-thumb:hover {
  background-color: rgba(102, 102, 102, 0.9);
}

section.exl-scrollable > div.exl-scrollable-scrollbar-wrapper > div.exl-scrollable-scrollbar-thumb:active {
  background-color: rgba(102, 102, 102, 1);
}

section.exl-scrollable > div.exl-scrollable-scrollbar-wrapper:hover > div.exl-scrollable-scrollbar-thumb {
  background-color: rgba(102, 102, 102, 0.9);
}

section.exl-scrollable > div.exl-scrollable-scrollbar-wrapper:before {
  content: "";
  position: absolute;
  right: 0;
  width: 10px;
  height: 2px;
  top: -2px;
  background-color: rgba(240, 240, 240, 0.3);
}

section.exl-scrollable > div.exl-scrollable-scrollbar-wrapper:after {
  content: "";
  position: absolute;
  right: 0;
  width: 10px;
  height: 2px;
  bottom: -2px;
  background-color: rgba(240, 240, 240, 0.3);
}

@-moz-document url-prefix() {
  section.exl-scrollable > div.exl-scrollable-scrollbar-wrapper {
    display: none;
  }
}
div.exl-todo-docker-wrap {
  position: fixed;
  bottom: 0;
  right: 24px;
  background-color: #2196f3;
  color: #ffffff;
}

div.exl-todo-docker-wrap ul {
  margin: 0;
  padding: 0;
}

div.exl-todo-docker-wrap button.pick {
  cursor: pointer;
  width: 100%;
  text-align: left;
  border: 0;
  padding: 10px;
  margin: 0;
  outline: none;
  background-color: inherit;
  color: inherit;
}

div.exl-todo-docker-wrap button.pick svg {
  vertical-align: middle;
  font-size: 24px;
}

div.exl-todo-docker-wrap button.pick span {
  vertical-align: middle;
  margin-left: 10px;
  font-size: 16px;
  font-family: "Ubuntu Mono", monospace;
}

div.exl-todo-docker-wrap div.pane {
  width: 320px;
  height: 400px;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: -400px;
  transition: margin-bottom 0.7s ease-in-out;
  font-size: 16px;
}

div.exl-todo-docker-wrap div.pane.open {
  margin-bottom: 0px;
}

div.exl-todo-docker-wrap div.pane input {
  vertical-align: middle;
  font-family: "Ubuntu Mono", monospace;
  font-size: inherit;
  background-color: transparent;
  color: #ffffff;
  border: 0;
  padding: 0;
  margin: 0;
  outline: none;
  width: calc(100% - 24px);
  margin-left: 10px;
}

div.exl-todo-docker-wrap div.pane button {
  vertical-align: middle;
  font-size: inherit;
  cursor: pointer;
  border: 0;
  padding: 0;
  margin: 0;
  outline: none;
  background-color: inherit;
  color: inherit;
}

div.exl-todo-docker-wrap div.pane ul.memoes {
  font-size: inherit;
  font-family: "Ubuntu Mono", monospace;
}
div.exl-todo-docker-wrap div.pane ul.memoes li {
  margin-top: 10px;
}

div.exl-todo-docker-wrap div.pane ul.memoes li button span {
  margin-left: 10px;
}

div.exl-todo-docker-wrap div.pane button.clean {
  cursor: pointer;
  border: 0;
  padding: 0;
  margin: 0;
  outline: none;
  background-color: inherit;
  color: inherit;
  margin-top: 10px;
}

div.exl-todo-docker-wrap div.pane button.clean svg {
  vertical-align: middle;
}

div.exl-todo-docker-wrap div.pane button.clean span {
  margin-left: 10px;
  vertical-align: middle;
  font-size: 16px;
  font-family: "Ubuntu Mono", monospace;
}

